import React, { Component } from "react";

import "./todo-list-item.css";

export default class TodoListItem extends Component {
  render() {
    const { label, onDeleted, onToggleImportant, onToggleDone, done, important } = this.props;

    let classNames = "todo-list-item";
    if (done) {
      classNames += " done";
    }

    if (important) {
      classNames += " important";
    }

    return (
      <span className={classNames}>
        <span>
          {label}
        </span>

        <ul className="ant-list-item-action">
          {/* <li>
            <button
              type="button"
              className="ant-btn ant-btn-circle ant-btn-icon-only ant-tooltip-open ant-btn-warning"
              onClick={onToggleImportant}
            >
              <span role="img" aria-label="exclamation" className="anticon anticon-exclamation">
                <svg
                  viewBox="64 64 896 896"
                  focusable="false"
                  data-icon="exclamation"
                  width="1em"
                  height="1em"
                  fill="currentColor"
                  aria-hidden="true"
                >
                  <path d="M448 804a64 64 0 10128 0 64 64 0 10-128 0zm32-168h64c4.4 0 8-3.6 8-8V164c0-4.4-3.6-8-8-8h-64c-4.4 0-8 3.6-8 8v464c0 4.4 3.6 8 8 8z"></path>
                </svg>
              </span>
            </button>
          </li> */}
          <li>
            <button
              type="button"
              className={done ? "ant-btn ant-btn-circle ant-btn-icon-only ant-tooltip-open ant-btn-primary" : "ant-btn ant-btn-circle ant-btn-icon-only ant-tooltip-open"}
              onClick={onToggleDone}
            >
              <span role="img" aria-label="check" className="anticon anticon-check">
                <svg
                  viewBox="64 64 896 896"
                  focusable="false"
                  data-icon="check"
                  width="1em"
                  height="1em"
                  fill="currentColor"
                  aria-hidden="true"
                >
                  <path d="M912 190h-69.9c-9.8 0-19.1 4.5-25.1 12.2L404.7 724.5 207 474a32 32 0 00-25.1-12.2H112c-6.7 0-10.4 7.7-6.3 12.9l273.9 347c12.8 16.2 37.4 16.2 50.3 0l488.4-618.9c4.1-5.1.4-12.8-6.3-12.8z"></path>
                </svg>
              </span>
            </button>
          </li>
          <li>
            <button
              type="button"
              className="ant-btn ant-btn-circle ant-btn-icon-only ant-tooltip-open ant-btn-dangerous"
              onClick={onDeleted}
            >
              <span role="img" aria-label="delete" className="anticon anticon-delete">
                <svg
                  viewBox="64 64 896 896"
                  focusable="false"
                  data-icon="delete"
                  width="1em"
                  height="1em"
                  fill="currentColor"
                  aria-hidden="true"
                >
                  <path d="M360 184h-8c4.4 0 8-3.6 8-8v8h304v-8c0 4.4 3.6 8 8 8h-8v72h72v-80c0-35.3-28.7-64-64-64H352c-35.3 0-64 28.7-64 64v80h72v-72zm504 72H160c-17.7 0-32 14.3-32 32v32c0 4.4 3.6 8 8 8h60.4l24.7 523c1.6 34.1 29.8 61 63.9 61h454c34.2 0 62.3-26.8 63.9-61l24.7-523H888c4.4 0 8-3.6 8-8v-32c0-17.7-14.3-32-32-32zM731.3 840H292.7l-24.2-512h487l-24.2 512z"></path>
                </svg>
              </span>
            </button>
          </li>
        </ul>
      </span>
    );
  }
}
